body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

body {
  margin: 0;
}

header {
  margin: 0;
  background-color: lightblue;
  background-image: url(/book-search-app-ei/static/media/books.6daa653a.jpg);
}

h1 {
  font-size: 45px;
  color: white;
  background-color: #00002598;
  padding: 20px;
  margin: 0;
}

.loading img {
  width: 100%;
  max-width: 200px;
}

.error {
  background-color: pink;
  position: absolute;
  left: 0;
  right: 0;
  width: 100%;
  top: 0;
  margin-left: auto;
  margin-right: auto;
  margin-top: 15px;
  padding-top: 10px;
}

.error button {
  display: flex;
}
.search-input {
    text-align: center;
    padding-bottom: 10px;
}

.search-term {
    padding: 10px;
}

input {
    margin: auto;
}

input[type='submit'], input[type='text'],
select {
    border: none;
    padding: 10px 20px;
    border-radius: 10px;
}

input[type='text'], select {
    display: block;
    border: 1px solid black;
    margin: 5px auto;
}

label {
    padding: 10px;
}

@media all and (min-width:600px) {
    input[type='text'], select {
        display: inline;
        margin-right: 15px;
    }
}

ul {
    list-style-type: none;
    padding-left: 0;
}

li {
    padding-bottom: 15px;
}

.group {
    display: flex;
    padding: 15px;
}

.item {
    flex: 1 1;
}

span {
    margin: 15px;
}

button {
    display: block;
    margin: 15px auto;
    border: none;
    padding: 10px 20px;
    border-radius: 10px;
}

p {
    width: 85%;
    margin: 15px auto;
}

li hr {
    margin: 30px 0;
}
