.search-input {
    text-align: center;
    padding-bottom: 10px;
}

.search-term {
    padding: 10px;
}

input {
    margin: auto;
}

input[type='submit'], input[type='text'],
select {
    border: none;
    padding: 10px 20px;
    border-radius: 10px;
}

input[type='text'], select {
    display: block;
    border: 1px solid black;
    margin: 5px auto;
}

label {
    padding: 10px;
}

@media all and (min-width:600px) {
    input[type='text'], select {
        display: inline;
        margin-right: 15px;
    }
}
